<template>
  <public>
    <html-head 
      title="Add Product"
    ></html-head>

    <page-header>
      <template v-slot:title >
        <span v-text="`Show Product`"></span>
      </template>
    </page-header>
    <v-card elevation="0" v-if="!product.isFetching && !product.isLoading">
      <v-card-text>
        <v-row>
          <v-col>
            <div class="d-flex justify-center">
              <v-avatar size="400" class="mb-3" tile>
                <img :src="getImage" ref="image"/>
              </v-avatar>
            </div>
          </v-col>
          <v-col>
            <div v-text="product.data.name" class="black--text text-h3"></div>
            <div class="text-caption text--disabled" v-text="`${ product.data.quantity } ${ product.data.unit }`"></div>
            <p class="mt-5 text--secondary text-subtitle-2" v-text="product.data.description"></p>
            <template v-if="hasVariations">
              <h2 class="text-subtitle-2 text--disabled">Variations</h2>    
              <v-tooltip bottom v-for="item in product.data.variations" :key="item.name">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"  
                    v-on="on"
                    icon
                    @click="setSelected( item )"
                  >
                    <v-avatar size="30">
                      <img :src="item.image" :alt="item.description">
                    </v-avatar> 
                  </v-btn>
                </template>
                <span v-text="item.description"></span>
              </v-tooltip>
            </template>
            <v-row class="mt-3">
              <v-col>   
                <h3 class="text-subtitle-2 text--disabled">Quantity:</h3>
                <div class="text-h4 black--text" v-text="`N/A`"></div>
              </v-col>
              <v-col>
                <div class="text-subtitle-2 text--disabled">Price:</div>  
                <div class="text-h4 black--text" v-text="getPrice"></div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </public>
</template>

<script>
import Api from './routes/api'
import Product from './models/Product'

export default {
  data: () => ({
    api: Api,
    product: new Product,
    selected: null
  }),
  
  computed: {
    getImage () {
      if( this.selected ) return this.selected.image
      return this.product.data.image 
    },

    getPrice () {
      if( this.selected ) return this.selected.price
      if( this.product.data.variations.length === 0 ) return this.product.data.price

      let 
        min = 0,
        max = 0
        
      for( const variation of this.product.data.variations ) {
        if( min > variation.price || min === 0) min = variation.price
        if( max < variation.price ) max = variation.price
      }

      return min !== max ? min + "~" + max : min
    },

    hasVariations () {  
      return this.product.data.variations.length !== 0
    }
  },

  methods: {
    setSelected ( item ) {
      this.selected = item
    },
  },                                                     
  
  mounted () {
    this.product.getResource( Api.single( this.$route.params.code ) )
    this.product.load( false )
  }
}
</script>